import {Switch, Route} from 'react-router-dom'
import { ManageCanceledMemberList } from '../pages/ManageCanceledMemberList';
import {SearchCanceledMember} from '../pages/SearchCanceledMember';

export function CanceledMemberList () {
    return (
        <Switch>
            <Route exact path='/member-list/canceled/'>
                <SearchCanceledMember/>
            </Route>
            <Route path='/member-list/canceled/:id'>
                <ManageCanceledMemberList/>
            </Route>
        </Switch>
    )
}

