import {useForm} from 'react-hook-form'
import { Link } from 'react-router-dom'


export function Login() {
    const {register, handleSubmit,watch} = useForm({})
    
    const submitPassed=()=>{
        console.log(watch())
    }
    const submitFailed=()=>{
        console.log(watch())
    }
    
    return (
        <div className='h-full flex flex-col justify-center items-center'>
            <img    className='mb-5 w-7/12'
                    src="https://dynaimage.cdn.cnn.com/cnn/c_fill,g_auto,w_1200,h_675,ar_16:9/https%3A%2F%2Fcdn.cnn.com%2Fcnnnext%2Fdam%2Fassets%2F210125162719-01-bernie-sanders-crochet-doll.jpg" alt="sanders sitting on chair"/>
            <form   onSubmit={handleSubmit(submitPassed, submitFailed)}
                    className='w-1/3 grid gap-5'>
                <div className='flex'>
                    <label htmlFor="form-id"
                        className='self-center text-sy-main w-10'
                    >ID</label>
                    <input ref={register({
                        required:"msssg"
                    })} name='id' type="text" id='form-id'
                        className='rounded-3xl border-2 border-sy-guide text-sy-guide w-full p-3'
                    />
                </div>
                <div className='flex'>
                    <label htmlFor="form-password"
                        className='self-center text-sy-main w-10'>
                        PW
                    </label>
                    <input ref={register({
                        required:"msssg"
                    })} name='password' type="password" id='form-password'
                        className='rounded-3xl border-2 border-sy-guide text-sy-guide w-full p-3'
                    />
                </div>
                <div className='mt-5 pl-10 grid grid-cols-2 gap-2'>
                    <button className='hover:bg-sy-blue-hover py-3 bg-sy-blue rounded-3xl text-white text-center'>
                        로그인
                    </button>
                    <span className='block hover:bg-sy-hover-btn bg-sy-main hover:bg-sy-main-hover rounded-3xl text-white text-center'>
                        <Link className='py-3 block' to='/signup'>
                            회원가입
                        </Link>
                    </span>
                </div>
            </form>
        </div>
    )
}