import { Login } from "./Login";
import { Switch, Route } from "react-router-dom";
import { Signup } from "./Signup";
import { Notice } from "./Notice";
import { ApproveMember } from "./ApproveMember";
import { Coupon } from "./Coupon";
import { Qna } from "./Qna";
import { MemberList } from "../Routers/MemberList";
import {CanceledMemberList} from '../Routers/CanceledMemberList'

export function MainBlock() {
    return (
        <div className=' h-main'>
            <Switch>
                <Route exact path='/login'>
                    <Login />
                </Route>
                <Route exact path='/signup'>
                    <Signup />
                </Route>
                <Route path='/approve-member'>
                    <ApproveMember/>
                </Route>
                <Route path='/member-list/canceled'>
                    <CanceledMemberList/>
                </Route>
                <Route path='/member-list'>
                    <MemberList/>
                </Route>
                <Route exact path='/'>
                    <Notice/>
                </Route>
                <Route path='/qna'>
                    <Qna/>
                </Route>
                <Route path='/coupon'>
                    <Coupon/>
                </Route>
            </Switch>
        </div>
    )
}

