import {Switch, Route} from 'react-router-dom'

import { ManageMemberList } from "../components/ManageMemberList"
import { SearchMemberList } from "../components/SearchMemberList";

export function MemberList () {
    return (
        <Switch>
            <Route exact path='/member-list/'>
                    <SearchMemberList/>
            </Route>
            <Route path='/member-list/:id'>
                    <ManageMemberList/>
            </Route>
        </Switch>
    )
}

