import { useState } from "react"
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

interface props {
    criteriaName:string;
    data:string[]
}
export const Criteria = ({criteriaName, data}:props) => {
    const [isOpen, setOpen] = useState(false)

    criteriaName.toUpperCase()
    return (
        <div className='relative mx-1 cursor-pointer mb-4 inline-block checked:text-sy-blue min-w-max'>
            <span className='inline-block hover:text-sy-blue-hover py-2 px-4 border border-sy-guide rounded-3xl hover:border-sy-blue-hover'
                  onClick={(e:React.MouseEvent<HTMLSpanElement, MouseEvent>)=>{
                    setOpen(true)
                    e.currentTarget.style.borderColor = 'transparent'
                  }}>

                {criteriaName} <ExpandMoreIcon/>
            </span>
        {isOpen && (
            <ul className='absolute overflow-hidden w-full left-0 right-0 top-0 min-w-max border border-sy-guide rounded-3xl bg-white cursor-default'>
                <li onClick={()=>setOpen(false)}
                    className='bg-sy-blue py-2 px-4 text-white cursor-pointer'>{criteriaName} <ExpandMoreIcon/></li>
                {
                    data.map((item:string, idx:number)=>{
                            return <li key={idx} className='py-2 px-4 cursor-pointer hover:bg-sy-blue-hover hover:text-white'>{item}</li>
                    })
                }
            </ul>
        )}
        </div>
    )
}

